@use "../base/variables" as *;
@use "../base/mixins" as m;
.header {
	background: $verde;
	@include m.tablet {
		&.fixed {
			position: fixed;
			top: 0;
			width: 100%;
			box-shadow: 0.1rem 0.2rem 0.3rem #333;
		}
	}

	.contenido-header {
		padding: 2rem;
		@include m.tablet {
			display: flex;
			justify-content: space-between;
			align-items: center;
		}
	}
	h1,
	a {
		color: $blanco;
		text-align: center;
		@include m.tablet {
			margin: 0;
			text-align: left;
		}
	}
	.navegacion-principal {
		display: flex;
		flex-direction: column;
		gap: 2rem;
		@include m.tablet {
			flex-direction: row;
		}
		a {
			font-size: 2.4rem;
			&:hover, &.active {
				color: $amarillo;
			}
		}
	}
}
