@use "./variables" as *;
@mixin movil {
	@media (min-width: $movil) {
		@content;
	}
}
@mixin tablet {
	@media (min-width: $tablet) {
		@content;
	}
}
@mixin desktop {
	@media (min-width: $desktop) {
		@content;
	}
}
@mixin desktopXL {
	@media (min-width: $desktopXL) {
		@content;
	}
}
@mixin contenedor {
	width: 95%;
	max-width: 120rem;
	margin: 0 auto;
}
@mixin grid($columnas: 1, $gap: 5rem) {
	display: grid;
	grid-template-columns: repeat($columnas, 1fr);
	gap: $gap;
}
@mixin resetear-lista {
    margin: 0;
    padding: 0;
    list-style: none;
}   